.text-block {
  text-align: left;
  max-width: 50rem;
  margin: 2.25rem auto 0;
  font-size: 1.25rem;
}

@media (min-width: 47.5rem) {
  .text-block {
    margin-top: 4.5rem;
  }
}

.text-block h2 {
  color: #0d7e5d;
  margin-bottom: 20px;
  font-size: 1.25rem;
  line-height: 1.2;
}

@media (min-width: 47.5rem) {
  .text-block h2 {
    font-size: 2rem;
  }
}

.text-block p + p {
  margin-top: 22px;
}

* {
  box-sizing: border-box;
}

:root {
  --bg-color: #fed67a;
  --on-bg-color: #ffedc1;
  --text-color: #02241a;
  --font-display: "Grandstander", cursive;
  --font-body: "Rubik", sans-serif;
}

html, body {
  height: 100%;
}

body {
  background: var(--bg-color);
  color: var(--text-color);
  font-family: var(--font-body);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  flex-direction: column;
  margin: 0;
  display: flex;
}

main {
  flex: 1 0 auto;
}

.wrapper {
  max-width: 81.25rem;
  width: 100%;
  text-align: center;
  margin: 0 auto;
  padding: 0 15px;
}

.wrapper header {
  margin-bottom: 1.125rem;
  padding: 20px 0;
}

@media (min-width: 47.5rem) {
  .wrapper header {
    margin-bottom: 4.5rem;
  }
}

.wrapper header figure {
  width: 120px;
  margin: 0 2.5rem;
}

.wrapper header a {
  font-family: var(--font-display);
  text-transform: uppercase;
  border: none;
  font-size: 1.25rem;
  font-weight: 700;
  text-decoration: none;
}

.wrapper header nav {
  align-items: center;
  display: flex;
}

.wrapper header nav a:first-child {
  margin-left: auto;
}

.wrapper header nav a:last-child {
  margin-right: auto;
}

.wrapper header h1 {
  margin-top: 4.5rem;
}

@media (min-width: 47.5rem) {
  .wrapper header {
    padding: 36px 0 0;
  }

  .wrapper header figure {
    width: 180px;
  }
}

.logo-wrap {
  height: 0;
  padding-bottom: 45.2%;
}

figure img {
  width: 100%;
  display: block;
}

h1 {
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-family: var(--font-display);
  margin: 0;
  font-weight: 800;
}

@media (min-width: 47.5rem) {
  h1 {
    font-size: 2.5rem;
    line-height: 2.625rem;
  }
}

h2 {
  font-family: var(--font-display);
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1.375rem;
  line-height: 1.375rem;
}

span {
  font-family: var(--font-display);
  margin-top: .625rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.125rem;
  display: block;
}

p {
  margin: 0;
  line-height: 1.4;
}

.translate-sections {
  margin-top: 2.25rem;
}

@media (min-width: 47.5rem) {
  .translate-sections {
    margin-top: 4.5rem;
  }
}

.translate-sections h2 {
  min-height: 1.5rem;
}

.translate-sections > section {
  width: 100%;
  max-width: 40rem;
}

.translate-sections .button {
  aspect-ratio: 2 / 1;
  margin-top: 1.25rem;
  margin-bottom: 3.125rem;
  box-shadow: 0 0 2rem #0000001a;
}

@media (min-width: 47.5rem) {
  .translate-sections {
    align-items: center;
    gap: 1rem;
    margin-bottom: 0;
    display: flex;
  }

  .translate-sections .button {
    aspect-ratio: 1.5 / 1;
    margin-top: 2.125rem;
    margin-bottom: 0;
  }
}

textarea {
  background: var(--on-bg-color);
  color: #000000de;
  font-family: var(--font-body);
  min-height: 12.5rem;
  resize: vertical;
  width: 100%;
  border: 5px solid #e5c272;
  border-radius: 20px;
  padding: .785714em 1em;
  font-size: 1.125rem;
  line-height: 1.2857;
  transition: all .15s;
}

textarea:focus {
  outline: 0;
}

@media (min-width: 47.5rem) {
  textarea {
    font-size: 1.375rem;
  }
}

button {
  cursor: pointer;
  font-family: var(--font-body);
}

.form-header {
  position: relative;
}

.form-header button {
  color: #4a4a4a;
  background: none;
  border: 0;
  font-size: 12px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.nederlands-input textarea:focus {
  background: #fff;
  border-color: #c1a361;
  box-shadow: 0 0 2rem #0000001a;
}

.haags-resultaat {
  position: relative;
}

.haags-resultaat button {
  color: #4a4a4a;
  background: none;
  border: 0;
  padding: 20px 5px 5px 15px;
  font-size: 12px;
  position: absolute;
  bottom: 15px;
  right: 0;
}

.haags-resultaat textarea {
  background: #e5c272;
}

.button {
  vertical-align: baseline;
  color: #fff;
  text-align: center;
  font-style: normal;
  line-height: 2.75rem;
  font-family: var(--font-display);
  background: #0d7e5d;
  border: none;
  border-radius: 20px;
  margin: 0 0 20px;
  padding: 0 20px;
  font-size: 1.5rem;
  font-weight: 700;
  transition: all .2s ease-in-out;
  display: inline-block;
}

.button:hover {
  background-color: #229c79;
  transform: scale(1.05);
}

@media (min-width: 47.5rem) {
  .button {
    font-size: 2rem;
  }
}

a {
  color: #3e3e3e;
  text-decoration: none;
}

nav a, p a {
  border-bottom: 1px solid;
}

nav a:hover, p a:hover {
  border: none;
}

footer {
  text-align: center;
  flex-shrink: 0;
  margin-top: 30px;
  padding-bottom: 30px;
}

footer p {
  margin-top: 10px;
  font-size: .875rem;
  line-height: 1rem;
}

footer a {
  font-family: var(--font-display);
  font-weight: 500;
}

